<template>
  <view id="home" class="padding-bottom-normal">
    <view class="home-header margin-normal">
      <view class="header-avatar bg-white">
        <image style="width: 46px; height: 46px;" :src="headerAvatar"/>
      </view>
      <view class="header-content">
        <view class="title">
          <text>欢迎</text>
          <!-- <text style="font-weight: bolder;">{{ userData.staffName }}</text> -->
        </view>
        <view class="sub">
          <text>{{ userData.comName }}</text>
        </view>
      </view>
    </view>
    <view class="menus-wrapper">
      <view class="menus margin-normal padding-normal radius-normal" v-show="menuLists.length">
        <view class="item" v-for="(menu) in menuLists" @click="forMenuPage(menu)" :key="menu.id">
          <view class="item-icon">
            <image style="width: 23px; height: 23px;" :src="menu.appIcon"/>
          </view>
          <view class="item-name">
            <text>{{ menu.name }}</text>
          </view>
        </view>
      </view>
    </view>

    <!-- <view class="task margin-normal">
      <common-header :title="taskTitle">
        <view slot="operation">
          <common-select
              :defaultTitle="typeDefaultTitle"
              @selecting="statusEmit"
              ref="type">
            <view slot="selectArea">
              <u-select
                  title="选择类型"
                  :default-value="typeDefaultV"
                  mode="single-column"
                  v-model="selectingType"
                  @confirm="typeConfirm"
                  @cancel="typeCancel"
                  :list="typeList"></u-select>
            </view>
          </common-select>
        </view>
      </common-header>
      <view class="task-lists radius-normal bg-white task-body" v-if="!empty && taskLists.length">
        <nnkjc-task-item
            v-for="(task, index) in taskLists"
            :taskTitle="task.seqNo"
            :taskTime="task.createTime"
            :taskContent="task.content"
            :iconClass="task.type"
            :taskType="task.type"
            :statusText="task.statusText"
            :overdueFlag="task.overdueFlag"
            :statusFlag="task.statusFlag"
            @single-click="forDetailPage(task)"
            :key="index">
        </nnkjc-task-item>
      </view>
    </view> -->
    <u-toast ref="uToast"/>
    <u-empty :show="empty" text="暂无待办任务" mode="list"></u-empty>
  </view>
</template>

<script>
  import CONFIG from "../../config"

  export default {
    data() {
      return {
        typeDefaultTitle: '全部类型',
        typeDefaultV: [0],
        selectingType: false,  //  是否按照状态选择
        typeList: [
          {
            value: '',
            label: '全部类型'
          },
          {
            value: 'weixiu',
            label: '维修单'
          },
          {
            value: 'xuncha',
            label: '巡查单'
          },
          {
            value: 'weihu',
            label: '保养单'
          }
        ],
        statusTagMap: CONFIG.statusTagMap,
        empty: false,
        taskTitle: "工作台",
        staticIcon: "../../static/image/home/makeorder.png",
        taskLists: [],
        searchParams: {
          pageNum: 1,
          pageSize: 10,
          type: ''
        },
        menuListsStatic: [
        {
          id: "1",
          name: " 考勤打卡",
          href: "",
          func: "ring",
          appIcon: "../../static/image/home/ring.png"
        }],
        userData: {
          staffName: "",
          comName: "",
        },
        headerAvatar: "../../static/image/home/head.png",
        menuLists: []
      };
    },
    onLoad(options) {
    },
    onShow() {
      this.getAPPMenuList();
      // this.getUserData();

      // this.getTaskListForAll();
      // const freshTask = this.$store.state.freshTask;

      wx.startWifi({
      success(res) {
        //获取当前已连接wifi名
        wx.getConnectedWifi({
          success: function (res) {
            console.log(res.wifi.SSID);
          },
        });
      },
    });
    },
    onReady() {
    },
    onHide() {
    },
    onUnload() {

    },
    onResize() {
    },
    onPageScroll() {
    },
    onPullDownRefresh() {
      this.searchParams.pageSize = 10;
      this.searchParams.pageNum = 1;
      this.taskLists = [];
      this.getTaskListForAll();
      uni.stopPullDownRefresh();
    },
    onReachBottom() {
      //列表是分页
      this.searchParams.pageNum++;
      this.getTaskListForAll();
    },
    methods: {
      typeCancel(v) {
        this.$refs["type"].selecting = false;
      },
      statusEmit(v) {
        this.selectingType = v;
      },
      typeConfirm(v) {
        this.$refs["type"].selecting = false;
        this.searchParams.pageNum = 1;
        this.searchParams.type = v[0].value;
        this.typeDefaultTitle = v[0].label;
        for (let i = 0; i < this.typeList.length; i++) {
          if (this.typeList[i].value === v[0].value) {
            this.typeDefaultV = [i]
            break;
          }
        }
        this.taskLists = [];
        this.getTaskListForAll();
      },
      //  单个详情页面
      forDetailPage(task) {
        if(!task.status || !task.type) {
          return
        }
        if(task.id) {
          const params = this.$u.queryParams({
            taskId: task.id,
            status: task.status,
            type: task.type
          })
          uni.navigateTo({
            url: CONFIG.ROUTE_MAP[task.type][task.status] + params
          });
        }
      },
      //  任务列表
      getTaskListForAll() {
        if(this.searchParams.pageNum === 1) {
          this.taskLists = [];
        }
        this.$u
            .post("/maint/getTaskList", this.searchParams)
            .then(res => {
              if (res.data.rows.length > 0) {
                for (let i = 0; i < res.data.rows.length; i++) {
                }
                this.taskLists.push(...res.data.rows);
              } else {
                if (this.searchParams.pageNum !== 1) {
                  // this.$refs.uToast.show({
                  //   title: "没有更多数据了~",
                  //   type: "warning"
                  // });
                }
              }
              this.empty = this.taskLists.length === 0;
            })
            .catch(err => {
              console.log(err);
            });
      },
      //  menu 页面跳转
      forMenuPage(menu) {
        const this_ = this;
        if (menu.func) {
          switch (menu.func) {
            case "ring":  //  打卡
              uni.navigateTo({
                url: "./clock-in/index"
              });
              break;
            }
        }
      },
      getUserData() {
      },
      getAPPMenuList() {
        this.menuLists = this.menuListsStatic
      }
    }
  };
</script>

<style scoped lang="scss">
  $header-width: 46px;
  #home {
    padding-top: 70px;
  }
  .home-header {
    display: flex;
    justify-content: space-between;
    > .header-avatar {
      width: $header-width;
      height: $header-width;
      min-width: $header-width;
      min-height: $header-width;
      flex-basis: $header-width;
      border-radius: 50%;
      text-align: center;
      overflow: hidden;
      > img {
        height: 100%;
        width: 100%;
        object-fit: contain;
      }
    }

    > .header-content {
      flex-grow: 1;
      padding-left: 12px;
      text-align: left;

      > .title {
        font-size: 20px;
      }

      > .sub {
        color: #8A8A8E;
        font-size: 12px;
      }
    }
  }

  .menus-wrapper {
  }

  .menus {
    background: rgba(255, 255, 255, 1);
    opacity: 1;
    margin-top: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }

  .item {
    width: 25%;
    display: flex;
    margin-top: 16px;
    flex-direction: column;
    text-align: center;
    justify-content: space-between;

    &:nth-of-type(4n+1) > .item-icon {
      background-color: rgba(72, 105, 254, 0.12);
    }

    &:nth-of-type(4n+2) > .item-icon {
      background-color: rgba(101, 32, 255, 0.12);
    }

    &:nth-of-type(4n+3) > .item-icon {
      background-color: rgba(36, 162, 124, 0.12);
    }

    &:nth-of-type(4n) > .item-icon {
      background-color: rgba(244, 81, 108, 0.12);
    }
  }

  .item-icon {
    margin: 0 auto;
    width: 46px;
    height: 46px;
    box-shadow: 0 0 0 8px #ffffff, 0 0 0 9px #f1f1f1;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    > img {
      width: 22px;
      height: 20px;
    }

  }

  .item-icon image {
    width: 46px;
    height: 46px;
  }

  .item-name {
    height: 20px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: rgba(0, 0, 0, 1);
    opacity: 1;
    margin-top: 20px;
  }

  .task-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .task-header .header {
    height: 26px;
    font-size: 18px;
    font-weight: bold;
    line-height: 26px;
    color: rgba(51, 51, 51, 1);
  }

  .task-opera-icon {
    height: 17px;
    font-size: 12px;
    font-weight: 400;
    color: rgba(51, 51, 51, 1);
  }

  .task-lists {
    margin-top: 8px;
  }

  .task-list-icon image {
    width: 44px;
    height: 44px;
  }


  .task-sub text {
    line-height: 25px;
  }

  .task-body {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  .task-tag image {
    height: 20px;
    width: 40px;
  }

  .task-tag img {
    height: 20px;
  }
</style>

